import React, { useEffect, useState } from "react";
import { CCard, CButton, CCol, CAlert } from "@coreui/react";
import { useSelector } from "react-redux";
import { selectUser } from "../../localyStore";
import axios from "axios";
import { CKEditor } from "@ckeditor/ckeditor5-react";
import ClassicEditor from "@ckeditor/ckeditor5-build-classic";
import { Add, Clear } from "@material-ui/icons";
import { ImageHandler } from "src/reusable/imageHandeller";

const server = process.env.REACT_APP_SERVER;

const Product = ({ match }) => {
  // const [categories, setCategories] = useState(null);
  // const [mainCategories, setMainCategories] = useState(null);
  const [productData, setProductData] = useState();
  const { token, role } = useSelector(selectUser);
  const [reload, setReload] = useState(null);
  const [longDescription, setLongDescription] = useState(null);
  // alert
  const [event, setEvent] = useState(null);

  const clearEvent = (ms) => {
    setTimeout(() => {
      setEvent(null);
    }, ms);
  };

  useEffect(() => {
    let Product;
    if (token) {
      if (role === "vendor") {
        Product =
          process.env.REACT_APP_ADMIN_PRODUCT_VENDOR_GET + match.params.id;
      } else if (role === "admin") {
        Product = process.env.REACT_APP_ADMIN_PRODUCT_GET + match.params.id;
      } else {
        Product = process.env.REACT_APP_ADMIN_PRODUCT_GET + match.params.id;
      }
      axios
        .get(server + Product)
        .then((data) => {
          setProductData(data?.data.products);
        })
        .catch((err) => {
          setEvent({
            type: "error",
            massage: err.response.data.message,
          });
          clearEvent(5000);
        });
    }
  }, [token, role, match.params.id, reload]);

  // image handler for img
  const imageHandleProduct = async (replace, img, deleteimg) => {
    let type = "adminProduct";
    const result = await ImageHandler(
      match.params.id,
      "product",
      null,
      img,
      deleteimg,
      token,
      type
    );
    if (result) {
      setReload(!reload);
      if (deleteimg) {
        setEvent({
          type: "error",
          massage: "images deleted",
        });
        clearEvent(8000);
      } else {
        setEvent({
          type: "success",
          massage: "images added",
        });
        clearEvent(8000);
      }
    } else {
      setEvent({
        type: "error",
        massage: "error on img update",
      });
      clearEvent(5000);
    }
  };

  // products update
  const productUpdate = (e) => {
    e.preventDefault();
    const data = e.target;
    axios
      .put(
        server + process.env.REACT_APP_ADMIN_PRODUCT_UPDATE,
        {
          productId: match.params.id,
          name: data.title.value,
          price: data.price.value,
          discountPrice: data.discountPrice.value,
          quantity: data.quantity.value,
          shortDescription: data.shortDescription.value,
          longDescription: longDescription,
          seoTitle: data.seoTitle.value,
          seoDescription: data.seoDescription.value,
          seoAlt: data.seoAlt.value,
          mainCategory: data.mainCategory?.value,
          sub1Category: data.sub1Category.value,
          sub2Category: data.sub2Category.value,
          tag: data.tag.value,
          weight: data.weight.value,
          ram: data.ram.value,
          storage: data.storage.value,
          color: data.color.value,
        },
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      )
      .then((res) => {
        setReload(!reload);
        setEvent({
          type: "success",
          massage: res.data.message,
        });
        clearEvent(5000);
      })
      .catch((err) => {
        console.log(err);
        setEvent({
          type: "error",
          massage: err.response.data.message,
        });
        clearEvent(5000);
      });
  };
  return (
    <div>
      <CCard className="p-4">
        <div className="d-flex flex-row">
          <CCol sm="2" className=" d-md-block">
            <div className="form-group">
              <label htmlFor="exampleFormControlSelect1">
                <h2>
                  Edit Category
                  <hr />
                </h2>
              </label>
              <select className="form-control" id="exampleFormControlSelect1">
                <option>{productData?.mainCategory?.name}</option>
              </select>
            </div>
            <div className="form-group">
              <label htmlFor="exampleFormControlSelect2">
                Select Sub-Category
              </label>
              <select className="form-control" id="exampleFormControlSelect2">
                <option>{productData?.sub1Category?.name}</option>
              </select>
            </div>
            <div className="form-group">
              <label htmlFor="exampleFormControlSelect2">
                Select Sub-Category
              </label>
              <select
                multiple
                className="form-control"
                id="exampleFormControlSelect2"
              >
                <option>{productData?.sub2Category?.name}</option>
              </select>
            </div>
          </CCol>
          <CCol sm="6">
            <form onSubmit={productUpdate}>
              <div className="flex-row">
                <div className="forms">
                  <div className="form-group">
                    <h2>Product Description</h2> <hr />
                    <div className="form-group">
                      <label htmlFor="exampleFormControlTextarea1">
                        Product Title
                      </label>
                      <input
                        defaultValue={productData?.name}
                        type="text"
                        name="title"
                        className="form-control"
                        placeholder="Product Title"
                      />
                    </div>
                    <input
                      defaultValue={productData?.mainCategory?._id}
                      name="mainCategory"
                      hidden
                    />
                    <input
                      defaultValue={productData?.sub1Category?._id}
                      name="sub1Category"
                      hidden
                    />
                    <input
                      defaultValue={productData?.sub2Category?._id}
                      name="sub2Category"
                      hidden
                    />
                    <div className="form-group">
                      <label htmlFor="exampleFormControlTextarea1">
                        Short Description
                      </label>
                      <textarea
                        defaultValue={productData?.shortDescription}
                        type="text"
                        className="form-control"
                        name="shortDescription"
                        placeholder="Give me a short description about product"
                      />
                    </div>
                    <label htmlFor="price">Price Rs.</label>
                    <input
                      required
                      defaultValue={productData?.price}
                      name="price"
                      type="number"
                      className="form-control"
                      id="price"
                      placeholder="Rs."
                    />
                    <br />
                    <label htmlFor="discountPrice">discountPrice Rs.</label>
                    <input
                      name="discountPrice"
                      type="number"
                      className="form-control"
                      id="discountPrice"
                      placeholder="Rs."
                      defaultValue={productData?.discountPrice}
                    />
                  </div>
                  <div className="input-group mb-3">
                    <div className="input-group-prepend">
                      <span
                        className="input-group-text"
                        id="inputGroupFileAddon01"
                      >
                        Quantity
                      </span>
                      <input
                        defaultValue={productData?.quantity}
                        name="quantity"
                        type="number"
                        className="form-control"
                        placeholder="Select Quantity"
                      />
                    </div>
                  </div>
                  <div>
                    <div className="input-group mb-3">
                      <div className="input-group-prepend">
                        <span
                          className="input-group-text"
                          id="inputGroupFileAddon01"
                        >
                          Weight
                        </span>
                        <input
                          required
                          name="weight"
                          type="number"
                          className="form-control"
                          placeholder="weight"
                          min={0}
                          defaultValue={productData?.attributes?.weight}
                        />
                      </div>
                    </div>
                    <div className="input-group mb-3">
                      <div className="input-group-prepend">
                        <span
                          className="input-group-text"
                          id="inputGroupFileAddon01"
                        >
                          Ram
                        </span>
                        <input
                          name="ram"
                          type="text"
                          className="form-control"
                          placeholder="Optional..."
                          defaultValue={productData?.attributes?.ram}
                        />
                      </div>
                    </div>
                    <div className="input-group mb-3">
                      <div className="input-group-prepend">
                        <span
                          className="input-group-text"
                          id="inputGroupFileAddon01"
                        >
                          Storage
                        </span>
                        <input
                          name="storage"
                          type="text"
                          className="form-control"
                          placeholder="Optional..."
                          defaultValue={productData?.attributes?.storage}
                        />
                      </div>
                    </div>
                    <div className="input-group mb-3">
                      <div className="input-group-prepend">
                        <span
                          className="input-group-text"
                          id="inputGroupFileAddon01"
                        >
                          Size
                        </span>
                        <input
                          name="size"
                          type="text"
                          className="form-control"
                          placeholder="Optional..."
                          defaultValue={productData?.attributes?.size}
                        />
                      </div>
                    </div>
                    <div className="input-group mb-3">
                      <div className="input-group-prepend">
                        <span
                          className="input-group-text"
                          id="inputGroupFileAddon01"
                        >
                          color
                        </span>
                        <input
                          name="color"
                          type="text"
                          className="form-control"
                          placeholder="Optional..."
                          defaultValue={productData?.attributes?.color}
                        />
                      </div>
                    </div>
                  </div>
                  {/* {used ckeditor 5 for product long description} */}
                  <h4>LongDescription</h4>
                  <CKEditor
                    editor={ClassicEditor}
                    data={productData?.longDescription}
                    onChange={(event, editor) => {
                      const data = editor.getData();
                      setLongDescription(data);
                    }}
                  />
                  <hr />
                  <label>SEOTitle</label>
                  <input
                    required
                    name="seoTitle"
                    type="text"
                    className="form-control"
                    placeholder="SEOTitle"
                    defaultValue={productData?.seoTitle}
                  />
                  <label>SEODescription</label>
                  <textarea
                    required
                    name="seoDescription"
                    type="text"
                    className="form-control"
                    placeholder="SEODescription"
                    defaultValue={productData?.seoDescription}
                  />
                  <label>SEOAlt</label>
                  <input
                    required
                    name="seoAlt"
                    type="text"
                    className="form-control"
                    placeholder="SEOAlt"
                    defaultValue={productData?.seoAlt}
                  />
                  <label>Tag</label>
                  <input
                    required
                    name="tag"
                    type="text"
                    className="form-control"
                    placeholder="tag"
                    defaultValue={productData?.tag}
                  />
                  <br />
                </div>
              </div>
              <CButton type="submit" style={{ backgroundColor: "#03A89E" }}>
                Save Product
              </CButton>
            </form>
          </CCol>
          <CCol sm="4">
            <CCard className="w-100">
              <h4 className="ml-4 my-2">Product Image</h4>
              <hr />
              <div className="d-flex flex-wrap justify-content-start align-items-center  p-2">
                {productData?.productPictures?.map((i) => (
                  <div
                    style={{
                      position: "relative",
                      width: "30%",
                      height: "100px",
                      margin: "4px",
                    }}
                  >
                    <a href={i.img} target="_blank">
                      <img
                        key={i._id}
                        className="mb-4 p-img"
                        src={i.img}
                        alt="img"
                      />
                    </a>
                    <div
                      style={{ position: "absolute", top: 0, right: -4 }}
                      onClick={() => imageHandleProduct(null, null, i.keys)}
                    >
                      <div className="p-1 bg-danger text-white rounded-pill">
                        <Clear fontSize="small" />
                      </div>
                    </div>
                  </div>
                ))}
                <div className="mt-2 ml-4">
                  <label htmlFor="productImgNew">
                    <div className="p-2 bg-success text-white rounded-pill">
                      <Add />
                    </div>
                  </label>
                  {/* input for add new img */}
                  <input
                    type="file"
                    hidden
                    id="productImgNew"
                    accept="image/png, image/jpeg,image/jpg,image/svg,image/webp"
                    onChange={(e) =>
                      imageHandleProduct(null, e.target.files[0], null)
                    }
                  />
                </div>
              </div>
            </CCard>
          </CCol>
        </div>
      </CCard>
      {event?.type === "error" && (
        <div className="position-fixed fixed-top d-flex justify-content-center">
          <CAlert color="danger">{event.massage}</CAlert>
        </div>
      )}
      {event?.type === "success" && (
        <div className="position-fixed fixed-top d-flex justify-content-center">
          <CAlert color="success">{event.massage}</CAlert>
        </div>
      )}
    </div>
  );
};

export default Product;
